  background-color: #f2d0d5;
  border-left: 3px solid #d61834;
  display: flex;
  align-items: flex-start;
  color: #212129;
  font-size: 14px;
}

#payment-accordion .panel {
  text-align: left;
  margin-bottom: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 6px;
  border: solid 2px #919194;
  margin-top: 16px;
  padding: 3px 0 3px 26px;
}

