  flex-wrap: wrap;
}

#payment-accordion .panel .radio > label {
  font-size: 18px;
  padding: 16px 24px 18px 12px;
  letter-spacing: .8px;
  font-family: "Letter_medium", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.radio label, .checkbox label {
  min-height: 20px;
  padding-left: 20px;
  margin-bottom: 0;
  font-weight: 400;
  cursor: pointer;
}

small {
  font-size: 85%;
}

.styled-input {
  display: block;
  position: relative;
  padding-left: 30px !important;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.radio label, .checkbox label {
  min-height: 20px;
  padding-left: 20px;
  margin-bottom: 0;
  font-weight: 400;
  cursor: pointer;
}

#payment-accordion .panel .radio > label {
  font-size: 18px;
  letter-spacing: .8px;
  font-family: "Letter_medium", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.has-error {
  color: #bc111e;
