  min-width: 180px;
  padding: 0 15px;
  font-size: 16px;
  border-radius: 8px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

input {
  padding: 6px 9px;
  border: 2px solid #807370;
  display: block;
  width: 100%;
  height: 40px;
  border-radius: 3px;
  outline: 0;
  font-family: Arial, Helvetica, sans-serif;
